@font-face {
  font-family: "SFUIDisplay";
  font-display: swap;
  src: url("../resources/fonts/SFUIDisplay/SFUIDisplay-Light.woff2") format("woff2"), url("../resources/fonts/SFUIDisplay/SFUIDisplay-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "SFUIDisplay";
  font-display: swap;
  src: url("../resources/fonts/SFUIDisplay/SFUIDisplay-Regular.woff2") format("woff2"), url("../resources/fonts/SFUIDisplay/SFUIDisplay-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "SFUIDisplay";
  font-display: swap;
  src: url("../resources/fonts/SFUIDisplay/SFUIDisplay-Semibold.woff2") format("woff2"), url("../resources/fonts/SFUIDisplay/SFUIDisplay-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

img {
  vertical-align: top;
}

button {
  cursor: pointer;
}

html,
body {
  min-height: 100%;
  line-height: 1;
}

html {
  scroll-behavior: smooth;
}

body {
  min-width: 320px;
  font-family: "SFUIDisplay";
  color: #000;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  overflow-x: hidden;
  background-image: url(../img/global/background.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  background-attachment: fixed;
  padding-bottom: 172px;
}
@media (max-width: 768px) {
  body {
    padding-bottom: 191px;
  }
}
@media (max-width: 560px) {
  body {
    padding-bottom: 187px;
  }
}

.container {
  max-width: 1210px;
  padding: 0 15px;
  margin: 0 auto;
}

.header {
  padding: 24px 0;
}
@media (max-width: 768px) {
  .header__container {
    max-width: 610px;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  column-gap: 25px;
  row-gap: 17px;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header__logo {
  margin-right: 69px;
  cursor: pointer;
}
@media (max-width: 768px) {
  .header__logo {
    margin-right: 12px;
  }
}
.header__form {
  position: relative;
  flex-grow: 1;
  margin-right: auto;
  max-width: 692px;
  height: 36px;
}
@media (max-width: 1024px) {
  .header__form {
    margin-right: 90px;
  }
}
@media (max-width: 768px) {
  .header__form {
    margin-right: 0;
  }
}
@media (max-width: 560px) {
  .header__form {
    order: 1;
    flex-basis: 100%;
  }
}
.header__favourite {
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}
.header__favourite:hover {
  transform: scale(1.3);
}

.search__input {
  width: 100%;
  height: 100%;
  padding-left: 18px;
  padding-right: 46px;
  border-radius: 20px;
  outline: none;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  appearance: none;
}
.search__input::-webkit-search-cancel-button {
  display: none;
}
.search__submit {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
  background-color: transparent;
}

.catalog__container {
  max-width: 1226px;
}
@media (max-width: 1026px) {
  .catalog__container {
    padding: 0 14px;
  }
}
@media (max-width: 560px) {
  .catalog__container {
    padding: 0 11px;
  }
}
.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, 196px);
  gap: 4px;
  justify-content: center;
}
@media (max-width: 560px) {
  .catalog__list {
    grid-template-columns: repeat(auto-fill, 148px);
    gap: 2px;
  }
}
.catalog__item {
  display: flex;
  flex-direction: column;
}
.catalog__track {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 6px;
  background-color: transparent;
  border: 2px solid transparent;
  border-radius: 20px;
}
.catalog__track:hover .track__img-inner::after, .catalog__track:focus .track__img-inner::after {
  opacity: 1;
  transform: translate(-50%, -50%) rotateY(0);
}
.catalog__track:active .track__img-inner::after {
  opacity: 1;
  transform: translate(-50%, -50%) rotateY(0) scale(1.3);
}
@media (max-width: 560px) {
  .catalog__track {
    padding: 3px;
    border-width: 1px;
    border-radius: 14px;
  }
}
.catalog__track_active {
  border-color: #fff;
  background-color: rgba(255, 255, 255, 0.5);
  transition: all 0.3s ease-in-out;
}
.catalog__track_active .track__img-inner::after {
  opacity: 1;
  transform: translate(-50%, -50%) rotateY(0);
  background-image: url(../img/svg/pause.svg);
}
.catalog__track_active:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
.catalog__track_active:hover .track__img-inner::after {
  background-color: #fff;
}
.catalog__track_pause .track__img-inner::after {
  background-image: url(../img/svg/play.svg);
}
.catalog__show-more-inner {
  text-align: end;
  grid-column: 1/-1;
  justify-self: end;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  border: 2px solid #fff;
  margin-right: 7px;
}
@media (max-width: 560px) {
  .catalog__show-more-inner {
    margin-top: 8px;
  }
}
.catalog__show-more {
  position: relative;
  background-color: transparent;
  color: rgb(255, 255, 255);
  letter-spacing: 0.2px;
  padding: 4px 28px 4px 15px;
  font-weight: 600;
  font-size: 18px;
}
.catalog__show-more::before {
  position: absolute;
  content: url(../img/svg/arrow.svg);
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  line-height: 0;
}
@media (max-width: 560px) {
  .catalog__show-more {
    padding-right: 25px;
    font-size: 14px;
    line-height: 17px;
  }
}

.track__img-inner {
  position: relative;
  border-radius: 12px 12px 0 0;
  overflow: hidden;
  line-height: 0;
}
.track__img-inner::after {
  position: absolute;
  content: "";
  display: block;
  background: rgba(255, 255, 255, 0.8);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateY(90deg);
  background-image: url(../img/svg/play.svg);
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.track__img {
  width: 180px;
  height: 180px;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.track__info {
  border-radius: 0 0 12px 12px;
  background-color: #fff;
  padding: 14px 8px;
  flex-grow: 1;
  width: 100%;
  text-align: left;
  color: #000;
}
@media (max-width: 560px) {
  .track__info {
    padding: 8px;
  }
}
.track-info__title {
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  margin-bottom: 6px;
}
@media (max-width: 560px) {
  .track-info__title {
    font-size: 14px;
    line-height: 17px;
  }
}
.track-info__artist {
  font-size: 14px;
  line-height: 17px;
  font-weight: 300;
}
@media (max-width: 560px) {
  .track-info__artist {
    font-size: 12px;
    line-height: 14px;
  }
}

.player {
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100%;
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(6px);
  border-radius: 20px;
  max-width: 1228px;
  margin: 0 auto;
  transition: bottom 0.5s ease-in-out;
  padding: 24px;
}
@media (max-width: 1230px) {
  .player {
    left: 15px;
    right: 15px;
  }
}
@media (max-width: 968px) {
  .player {
    padding: 22px 60px;
    border-radius: 30px;
  }
}
@media (max-width: 768px) {
  .player {
    border-radius: 20px 20px 0 0;
  }
}
@media (max-width: 560px) {
  .player {
    padding: 15px 22px 19px;
    left: 0;
    right: 0;
  }
}
.player_active {
  bottom: 26px;
}
@media (max-width: 768px) {
  .player_active {
    bottom: 0;
  }
}
.player__container {
  display: grid;
  grid-template-columns: 1fr 1fr 3fr 1fr;
  align-items: center;
  gap: 20px;
  padding: 0;
}
@media (max-width: 1024px) {
  .player__container {
    grid-template-columns: 1fr 1fr 2fr 1fr;
  }
}
@media (max-width: 968px) {
  .player__container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .player__container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 560px) {
  .player__container {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}
@media (max-width: 560px) {
  .player__track-info {
    text-align: center;
  }
}
.player__controller {
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (max-width: 968px) {
  .player__controller {
    justify-self: center;
  }
}
@media (max-width: 768px) {
  .player__controller {
    justify-self: end;
  }
}
@media (max-width: 560px) {
  .player__controller {
    justify-self: center;
  }
}
.player__action {
  display: block;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
}
.player__action_stop {
  background-image: url(../img/svg/stop.svg);
}
.player__action_prev {
  background-image: url(../img/svg/prev.svg);
}
.player__action_pause {
  background-image: url(../img/svg/pause.svg);
}
.player__action_play {
  background-image: url(../img/svg/play.svg);
}
.player__action_next {
  background-image: url(../img/svg/next.svg);
}
.player__action_favourite {
  background-image: url(../img/svg/favourite_black.svg);
}
.player__action_favourite_active {
  background-image: url(../img/svg/favourite_pink.svg);
}
.player__action_mute {
  background-image: url(../img/svg/unmute.svg);
}
.player__action_unmute {
  background-image: url(../img/svg/mute.svg);
}
.player__progress-wrapper {
  display: flex;
  align-items: center;
  gap: 16px;
  padding-right: 9px;
}
@media (max-width: 968px) {
  .player__progress-wrapper {
    order: 1;
    grid-column: 1/-1;
    padding: 0;
  }
}
@media (max-width: 560px) {
  .player__progress-wrapper {
    flex-direction: column-reverse;
    gap: 5px;
  }
}
.player__progress {
  flex-grow: 1;
  max-width: 487px;
  line-height: 0;
  padding: 9px 0;
}
@media (max-width: 968px) {
  .player__progress {
    max-width: 100%;
  }
}
@media (max-width: 560px) {
  .player__progress {
    width: 100%;
  }
}
.player__range {
  width: 100%;
  cursor: pointer;
  background-color: transparent;
  appearance: none;
}
.player__range::-webkit-slider-runnable-track {
  background-color: #1e5a32;
  border: none;
  border-radius: 20px;
  height: 6px;
}
.player__range::-webkit-slider-thumb {
  appearance: none;
  margin-top: -9px;
  width: 12px;
  height: 24px;
  background: #ffffff;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}
.player__range::-moz-range-track {
  background-color: #1e5a32;
  border: none;
  border-radius: 20px;
  height: 6px;
}
.player__range::-moz-range-thumb {
  appearance: none;
  margin-top: -9px;
  width: 12px;
  height: 24px;
  background: #ffffff;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}
.player__time {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  min-width: 72px;
}
.player__volume-wrapper {
  display: flex;
  align-items: center;
  gap: 16px;
}
@media (max-width: 968px) {
  .player__volume-wrapper {
    justify-self: center;
  }
}
@media (max-width: 768px) {
  .player__volume-wrapper {
    display: none;
  }
}
.player__volume {
  line-height: 0;
  padding: 9px 0;
}
.player__volume-range {
  appearance: none;
  width: 100%;
  cursor: pointer;
  background-color: transparent;
}
.player__volume-range::-webkit-slider-runnable-track {
  background-color: #413939;
  border: none;
  border-radius: 20px;
  height: 6px;
}
.player__volume-range::-webkit-slider-thumb {
  appearance: none;
  margin-top: -9px;
  width: 12px;
  height: 24px;
  background: #ffffff;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}
.player__volume-range::-moz-range-track {
  background-color: #413939;
  border: none;
  border-radius: 20px;
  height: 6px;
}
.player__volume-range::-moz-range-thumb {
  appearance: none;
  margin-top: -9px;
  width: 12px;
  height: 24px;
  background: #ffffff;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}